// Standard response helpers
function successResponse(data: any, message: string = 'Success', meta: any = {}) {
  return NextResponse.json({
    success: true,
    message,
    data,
    meta
  })
}

function errorResponse(error: string, code: string = 'ERROR', status: number = 500) {
  return NextResponse.json({
    success: false,
    error,
    code
  }, { status })
}

